.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 1px 3px rgb(0 0 0 / 29%);
  padding: 10px;
  margin-bottom: 20px;
}

.year-chevron {
  display: flex;
  align-items: center;
}

.header-year {
  margin-left: 10px;
}

.header-heading {
  text-align: center;
  font-size: 20px;
}

.header-icons {
  display: flex;
}

.header-icon {
  margin-left: 12px;
}
